import { getCookie } from './config/mUtils' // Ë∞ÉÁî®getStore

Vue.use(ElementUi);

Vue.config.productionTip = false;

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) { // ÈúÄË¶ÅÊùÉÈôê,Ëøõ‰∏ÄÊ≠•ËøõË°åÂà§Êñ≠
    let isLogin = getCookie('hx2car_admin'); // Ëé∑Âèñ
    console.log(isLogin);
    if (isLogin) { // Ëé∑ÂèñÂΩìÂâçÁöÑuser_idÊòØÂê¶Â≠òÂú®
      next()
    } else { // Â¶ÇÊûúÊ≤°ÊúâÊùÉÈôê,ÈáçÂÆöÂêëÂà∞ÁôªÂΩïÈ°µ,ËøõË°åÁôªÂΩï
      next({
        path: '/login'
        // query: {redirect: to.fullPath}  // Â∞ÜË∑≥ËΩ¨ÁöÑË∑ØÁî±path‰Ωú‰∏∫ÂèÇÊï∞ÔºåÁôªÂΩïÊàêÂäüÂêéË∑≥ËΩ¨Âà∞ËØ•Ë∑ØÁî±
      })
    }
  } else { // ‰∏çÈúÄË¶ÅÊùÉÈôê Áõ¥Êé•Ë∑≥ËΩ¨
    next()
  }
});

//ËøáÊª§Âô®Áªü‰∏ÄÂ§ÑÁêÜÂáΩÊï∞
Object.keys(filterName).forEach(key =>{ //filterName‰∏∫‰º†ËøõÊù•ÁöÑËøáÊª§Âô®ÂêçÁß∞
  Vue.filter(key,filterName[key]);
});

/* eslint-disable no-new */
new Vue({
  el: '#hx',
  router,
  store,
  components: { App },
  template: '<App/>'
});



